<template>
  <div>
    <iframe src="./assets/examples/index.html" frameborder="0"></iframe>
  </div>
</template>

<script>
import { WebGLRenderer, Scene, PerspectiveCamera, LightProbe, DirectionalLight, CubeTextureLoader } from 'three'
export default {
  name: 'camera-logarithmicdepthbuffer',
  async mounted () {
    let windowWidth = window.innerWidth
    let windowHeight = window.innerHeight
    let renderer = new WebGLRenderer({ antialias: true })
    renderer.setPixelRatio(window.devicePixelRatio)
    renderer.setSize(windowWidth, windowHeight)
    document.getElementById('home').appendChild(renderer.domElement)
    renderer.gammaOutput = true
    renderer.gammaInput = 2.2

    let scene = new Scene()

    let camera = new PerspectiveCamera(40, windowWidth / windowHeight, 1, 1000)
    camera.position.set(0, 0, 30)

    let lightProbe = new LightProbe()
    scene.add(lightProbe)

    let directionalLight = new DirectionalLight(0xffffff, 0.2)
    directionalLight.position.set(10, 10, 10)
    scene.add(directionalLight)

    let prefix = '../assets/examples/textures/cube/pisa'
    let postfix = '.png'
    let urls = [
      prefix + 'px' + postfix, prefix + 'nx' + postfix,
      prefix + 'py' + postfix, prefix + 'ny' + postfix,
      prefix + 'pz' + postfix, prefix + 'nz' + postfix
    ]
    new CubeTextureLoader().load(urls, function (cubeTextures) {
      console.log(2222)
      console.log(cubeTextures)
    })
  }
}
</script>
